import React, { useState } from 'react';
import './Main.css';
import truck1 from '../../img/cysterna.jpg';
import truck2 from '../../img/hakowiec.jpg';
import truck3 from '../../img/zima.jpg';

const Main = () => {

    const [zoom1, setZoom1] = useState(false);
    const [zoom2, setZoom2] = useState(false);
    const [zoom3, setZoom3] = useState(false);

    return (
        <div className='mainContainerSection4' style={{ backgroundImage: `url(${truck3})`, backgroundSize: `${zoom3 ? '50vw 60vh' : '45vw 55vh'}` }}>
            <div className='shadowMainContainer'>
                <div className='leftContainer'>
                    <div className='leftBox' style={{ backgroundImage: `url(${truck1})`, backgroundSize: `${zoom1 ? '120% 120%' : '100% 100%'}` }}>
                        <div className='shadowLeftBox'>
                            <h2 onMouseOver={() => setZoom1(true)} onMouseLeave={() => setZoom1(false)}>Cysterny</h2>
                        </div>
                    </div>
                    <div className='triangleLeft'></div>
                </div>
                <h2 onMouseOver={() => setZoom3(true)} onMouseLeave={() => setZoom3(false)}>Zimowe utrzymanie dróg</h2>
                <div className='rightContainer'>
                    <div className='triangleRight'></div>
                    <div className='rightBox' style={{ backgroundImage: `url(${truck2})`, backgroundSize: `${zoom2 ? '120% 120%' : '100% 100%'}` }}>
                        <div className='shadowRightBox'>
                            <h2 onMouseOver={() => setZoom2(true)} onMouseLeave={() => setZoom2(false)}>Inne realizacje...</h2>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Main;